// import Engine from './utils/engine';
import anime from 'animejs/lib/anime.es.js';
import {BoxGeometry, MeshPhongMaterial, Mesh, OrthographicCamera, Group, Math as ThreeMath, AmbientLight, PointLight, CubeTextureLoader, RGBFormat } from 'three';
import Engine from './utils/engine3d';

import px from '../images/envMap/test-px.jpg';
import nx from '../images/envMap/test-nx.jpg';
import py from '../images/envMap/test-py.jpg';
import ny from '../images/envMap/test-ny.jpg';
import pz from '../images/envMap/test-pz.jpg';
import nz from '../images/envMap/test-nz.jpg';

const WIDTH = 480;
const HEIGHT = 480;

// const el = document.getElementById('canvas');
const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
}, {
  alpha: true
});

var cubeTextureLoader = new CubeTextureLoader();

var envMaps = ( function () {
  var urls = [px, nx, py, ny, pz, nz];

  var reflectionCube = cubeTextureLoader.load( urls );
  reflectionCube.format = RGBFormat;

  return reflectionCube

} )();


// const camera = new PerspectiveCamera(75, WIDTH / HEIGHT, 0.1, 1000);
// camera.position.z = 2;
const camera = new OrthographicCamera(WIDTH / -2, WIDTH / 2, HEIGHT / 2, HEIGHT / -2, 1, 1000);
camera.position.z = 500;

app.setCamera(camera, true, { enableZoom: true, autoRotate: true });

app.scene.add(new AmbientLight(0xFFFFFF));

const light = new PointLight(0xFFFFFF);
light.position.y = 500;
light.position.z = 500;
app.scene.add(light);

const amount = 10;
const size = 100;
const geo = new BoxGeometry(size, size, (size / amount) / 2);
const material = new MeshPhongMaterial({
  color: 0xcc3930,
  emissive: 0x702020,
  specular: 0xa1f38,
  shininess: 100,
  envMap: envMaps
  // map: texture
});

const group = new Group();

for (let i = 0; i < amount; i++) {
  const plane = new Mesh(geo, material);
  plane.position.set(0, 0, -(size / 2) + (i * (size / amount)));
  group.add(plane);
}

const anim = {
  x: 0,
  y: 0,
  z: 0
};

group.rotateY(Math.PI * 0.25);
group.rotateX(Math.PI * 0.25);

anime({
  targets: anim,
  x: 360,
  // y: Math.PI * 2,
  // z: Math.PI * 2,
  loop: true,
  duration: 2000,
  endDelay: 1000,
  easing: 'easeInOutQuint'
});

app.scene.add(group);

app.onRender(({ frame, width, height, debug }) => {
  // group.rotation.set(anim.x, anim.y, anim.z);
  group.rotation.x = ThreeMath.degToRad(anim.x);
  // group.rotateX(0.01);

  camera.updateProjectionMatrix();
});

app.start();
