import { Scene, OrthographicCamera, WebGLRenderer, PerspectiveCamera, PCFSoftShadowMap } from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';

import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass';
import { FilmPass } from 'three/examples/jsm/postprocessing/FilmPass';
import { BloomPass } from 'three/examples/jsm/postprocessing/BloomPass';

import BaseEngine from './baseEngine';

const DEFAULTS = {
  debug: false,
  width: 480,
  height: 480,
  pixelRatio: window.devicePixelRatio,
  clickToggleDebug: true,
  postProcessing: false
};

const RENDERER_DEFAULTS = {
  alpha: false
};

class SketchEngine3D extends BaseEngine {
  constructor(mountEl, opts = {}, rendererOpts = {}) {
    super(DEFAULTS, opts);

    this.rendererOptions = {
      ...RENDERER_DEFAULTS,
      ...rendererOpts
    };

    this.mountEl = mountEl;

    this.scene = new Scene();

    this.renderer = new WebGLRenderer(this.rendererOptions);
    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMap.type = PCFSoftShadowMap;

    this.mountEl.appendChild(this.renderer.domElement);

    this.updateDimensions(this.options.width, this.options.height, this.options.pixelRatio);
  }

  get wrapper() {
    return this.mountEl;
  }

  setCamera(camera, orbitControls = false, { autoRotate = false, enableZoom = false, enabled = true } = {}) {
    this.camera = camera;

    if (orbitControls) {
      this.orbit = new OrbitControls(this.camera, this.renderer.domElement);
      this.orbit.autoRotate = autoRotate;
      this.orbit.enableZoom = enableZoom;
      this.orbit.enabled = enabled;
    } else {
      this.orbit = null;
    }

    if (this.options.postProcessing) {
      this.addPostProcessing();
    }
  }

  addPostProcessing() {
    this.composer = new EffectComposer(this.renderer);

    this.renderScene = new RenderPass(this.scene, this.camera);
    this.composer.addPass(this.renderScene);

    // this.bloomPass = new BloomPass(
    //   5,    // strength
    //   12,   // kernel size
    //   20,    // sigma ?
    //   5,  // blur render target resolution
    // );
    // this.composer.addPass(this.bloomPass);

    // this.filmPass = new FilmPass(
    //   1,   // noise intensity
    //   0.5,  // scanline intensity
    //   1000,    // scanline count
    //   false,  // grayscale
    // );
    // this.composer.addPass(this.filmPass);
  }

  update(dt) {
    if (!this.camera) {
      console.log("There is no camera");

      return false;
    }

    this._renderer({
      debug: this.options.debug,
      frame: this.frame,
      deltaTime: dt,
      width: this.options.width,
      height: this.options.height
    });

    if (this.orbit) {
      this.orbit.update();
    }

    if (this.options.postProcessing) {
      this.composer.render();
    } else {
      this.renderer.render(this.scene, this.camera);
    }

    this.frame++;
  }

  static ORTHOPGRAPHIC_CAMERA(width, height, { near = 1, far = 1000} = {}) {
    const camera = new OrthographicCamera(width / -2, width / 2, height / 2, height / -2, near, far);
    camera.position.z = 500;
    camera.zoom = 2.5;
    camera.updateProjectionMatrix();
    return camera;
  }

  static PERSPECTIVE_CAMERA(width, height, { fov = 45, near = 1, far = 1000} = {}) {
    const camera = new PerspectiveCamera(fov, width / height, near, far);
    camera.position.z = 5;
    return camera;
  }
}

export default SketchEngine3D;
